import {Button, ButtonItem} from './styledComponents'

const GradientDirection = props => {
  const {listButton, changeButton, currentVal} = props
  const {directionId, value, displayText} = listButton

  const onClickButton = () => {
    changeButton(directionId)
  }

  return (
    <ButtonItem>
      <Button onClick={onClickButton} val={currentVal}>
        {displayText}
      </Button>
    </ButtonItem>
  )
}

export default GradientDirection
