import {Component} from 'react'
import {
  GeneratorContainer,
  Head,
  Discription,
  ButtonList,
  ColorContainer,
  Color,
  ColorgradientContainer,
  GenerateButton,
  Input1,
} from './styledComponents'
import GradientDirection from '../GradientDirectionItem'

const gradientDirectionsList = [
  {directionId: 'TOP', value: 'top', displayText: 'Top'},
  {directionId: 'BOTTOM', value: 'bottom', displayText: 'Bottom'},
  {directionId: 'RIGHT', value: 'right', displayText: 'Right'},
  {directionId: 'LEFT', value: 'left', displayText: 'Left'},
]
// Write your code here

class GradientGenerator extends Component {
  state = {
    color1: '#8ae323',
    color2: '#014f7b',
    currentDirection: gradientDirectionsList[0].directionId,
    changeColor1: '#8ae323',
    changeColor2: '#014f7b',
    setDerirection: gradientDirectionsList[0].value,
  }

  getInputColor1 = event => {
    this.setState({color1: event.target.value})
  }

  getInputColor2 = event => {
    this.setState({color2: event.target.value})
  }

  changeButton = id => {
    this.setState({currentDirection: id})
  }

  onClickButton = () => {
    const {color1, color2, currentDirection} = this.state
    this.setState({
      changeColor1: color1,
      changeColor2: color2,
      setDerirection: currentDirection.toLowerCase(),
    })
  }

  render() {
    const {
      color1,
      color2,
      changeColor1,
      changeColor2,
      currentDirection,
      setDerirection,
    } = this.state

    return (
      <GeneratorContainer
        direction={setDerirection}
        colorOne={changeColor1}
        colorTwo={changeColor2}
        data-testid="gradientGenerator"
      >
        <Head>Generate a CSS color gradient</Head>
        <Discription>Choose Direction</Discription>
        <ButtonList>
          {gradientDirectionsList.map(eachItem => (
            <GradientDirection
              listButton={eachItem}
              currentVal={eachItem.directionId === currentDirection}
              key={eachItem.directionId}
              changeButton={this.changeButton}
            />
          ))}
        </ButtonList>
        <Discription>Pick the colors</Discription>
        <ColorgradientContainer>
          <ColorContainer>
            <Color>{color1}</Color>
            <Input1
              type="color"
              value={color1}
              onChange={this.getInputColor1}
            />
          </ColorContainer>
          <ColorContainer>
            <Color>{color2}</Color>
            <Input1
              type="color"
              value={color2}
              onChange={this.getInputColor2}
            />
          </ColorContainer>
        </ColorgradientContainer>
        <GenerateButton onClick={this.onClickButton}>Generate</GenerateButton>
      </GeneratorContainer>
    )
  }
}

export default GradientGenerator
